.sidebarChat {
  display: flex;
  padding: 20px;
  cursor: pointer;
  border-bottom: 1px solid #f6f6f6;
}

.sidebarChat:hover {
  background-color: #ebebeb;
}

.sidebarChat__info > h2 {
  font-size: 14px;
  margin-bottom: 8px;
}

.sidebarChat__info {
  margin-left: 15px;
}
.sidebarChat__info > p {
  font-size: 12px;
}

a {
  color: #363636;
  text-decoration: none !important;
}
